html, body {
    width: 100%;
    margin: auto;
}

p {
    font-size: 24px;
}

.topnav {
  background-color: #333;
  overflow: hidden;
}

.topnav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

.topnav a.active {
  background-color: grey;
  color: white;
}

figcaption.front {
    font-size: 24px;
}

figure.side {
    padding-left: 5;
    font-size: 24px;
    width: 90%;
    margin: 0.5em;
}

figcaption.side {
    caption-side: right;
}

.container {
    text-indent: 5%;
    width: 95%;
}

.colpics {
    float: left;
    width: 40%;
    /*background: #acb3b9;*/
}
.coltext {
    text-indent: 0%;
    float: left;
    width: 55%;
    font-size: 24px;
}

img.col {
  max-width: 85%;
  height: auto;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

.flex-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    /*flex-wrap: wrap;*/
    /*background-color: DodgerBlue;*/
}

.flex-container > div {
  /*background-color: #f1f1f1;*/
  margin: 10px;
  padding: 10px;
  font-size: 24px;
}

.flex-banner {
    width: 100%;
    display: flex;
    flex-direction: row;
    /*flex-wrap: wrap;*/
    /*background-color: DodgerBlue;*/
}

.flex-banner > div {
  /*background-color: #f1f1f1;*/
  margin: 4px;
  padding: 2px;
  font-size: 40px;
}


.heading {
    margin: 20px;
}
